table{
    border-collapse: collapse;
    margin-top: 30px;
    margin-left: 30px;
    /* width:"1600px" */
}
th{
    border-bottom: 1px solid black;
    border: 2px solid black;
    font-size: 20px;
    padding: 10px;
    height: 40px;
    width: 150px;
}
td{
    text-align: center;
    border: 2px solid black;
    background-color: #fff;
    font-size: 20px;

}
.address{
    max-height: 5rem;
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    display: "-webkit-box";
    /* display: flex; */
    /* flex-wrap: wrap; */
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    background-color:transparent;
    overflow: hidden;
}
.back-btn{
    height: 50px;
    width: 80px;
    font-size: 20px;
    margin-left: 30px;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    color: white;
    background-color: #274167;
}
.back-btn:hover{
    background-color: #4c5f79;
}
