.table{
    border-collapse: collapse;
    border: 1px solid #d3d3d3;
    width: 100%;
    overflow-x: scroll;
    background-color: #fff;
    color: #333;
    font-size: 10px;
    
}
table {
    margin: 0px !important;
}

.thead{

    border: 1px solid #d3d3d3;
    background-color: rgb(195, 195, 195);
    
}

.tbody{
    text-align: center;
    border: 1px solid #d3d3d3;
    font-size: 10px;
}
.add-btn{
    height: 50px;
    width: 80px;
    border: none;
    font-size: 20px;
    background-color: #274167;
    color: white;
    cursor: pointer;
    border-radius: 50px;
}
.add-btn:hover{
    color: white ;
    border: none ;
    background-color: #39557f;
    border-radius: 50px;
}
.entry-btn{
    
    color: white;
    background-color: #274167;
    border: none;
    width: 100%;
    height: 6vh;
    cursor: pointer;
    font-size: 20px;
}
:hover.entry-btn{
    background-color: #526581;
}


.exit-btn{
    color: white;
    background-color: #274167;
    border: none;
    width: 100%;
    height: 6vh;
    cursor: pointer;
    font-size: 20px;
}


:hover.exit-btn{
    background-color: #526581;
}
.searchImage{
    height: 30px;
    width: 30px;
    padding: 7px;
}
.searchIcon{
    cursor: pointer;
    top: 3px;
    position: absolute;
    right: 2px;
    height: 30px;
    /* border-bottom-right-radius: 10px; */
    border-top-right-radius: 10px;
    background-color:white;
}


